const BlogsPage = () => {

  const [page, setCurrentPage] = useState(0);
  const [data, setData] = useState([]);

  const perPage = 8
  const offset = page * perPage
  const pageCount = Math.ceil(data.length / perPage);

  const handleChangepage = ({ selected }) => setCurrentPage(selected);

  const loadData = () => setData(blogs);
  useEffect(() => loadData(), []);

  const currentPageData = data.slice(offset, offset + perPage).map((ele, ind) => {
    console.log("ele::", ele);
    return (
      <Col sm={12} md={6} xl={4} xxl={3}>
        <Card className="blogs-card" key={ind}>
          <div className="card-img-hover">
            <Card.Img src={ele.img} alt="blog img" />
          </div>
          <Card.Body>
            <h5>{ele.name} </h5>
            <div className="d-flex my-2">
              <div>
                <i className="bi bi-pencil-square px-1"></i>
                <small>{ele.author}</small>
              </div>
              <div className="mx-4">
                <i className="bi bi-calendar3 px-1"></i>
                <small>{ele.create_date}</small>
              </div>
            </div>
            <p>{ele.description.slice(0, 100) + "..."}</p>
            {/* <Link className="read-more" to={`/blogs/${ele.uri}`}>
              <span><i class="bi bi-arrow-right-short"></i></span>
              Read more
              <i className="btn-icon fa fa-long-arrow-right"></i>
            </Link> */}
            <Link className="read-more" to={`/blogs/${ele.uri}`}>
              <i class="bi bi-arrow-right-short"></i>
              Read more
            </Link>
          </Card.Body>
        </Card>
      </Col >
    )
  });

  const prevText = (
    <>
      <span aria-hidden="true">
        {/* « */}
        <i className="bi bi-chevron-double-left"></i>
      </span> Prev
    </>
  );
  const nextText = (
    <>
      Next <span aria-hidden="true">
        {/* » */}
        <i className="bi bi-chevron-double-right"></i>
      </span>
    </>
  );

  const scrollToTop = () => window.scrollTo({ top: 0 });

  return (
    <Layout>
      <Container>
        <Row>
          {currentPageData}
        </Row>
        <div className="d-flex justify-content-end mt-5">
          <Pagination
            previousLabel={prevText}
            nextLabel={nextText}
            pageCount={pageCount}
            onPageChange={handleChangepage}
            containerClassName={"pagination"}
            previousLinkClassName={"pagination__link"}
            nextLinkClassName={"pagination__link"}
            disabledClassName={"pagination__link--disabled"}
            activeClassName={"pagination__link--active"}
            onClick={scrollToTop}
          />
        </div>
      </Container>
    </Layout>
  )
};

export const Head = () => <Seo title="Flipflop Team Blogs" />

export default BlogsPage;
